import { ReactNode } from "react";

export default function Section({ title, subtitle, tone = "default", children } : { title?:string, subtitle?:string, tone?: "default"|"raised", children?: ReactNode }){
  return (
    <section className={`section ${tone === "raised" ? "raised": ""}`}>
      <div className="container">
        {title && <h2 style={{margin:"0 0 8px"}}>{title}</h2>}
        {subtitle && <p className="muted" style={{margin:"0 0 16px"}}>{subtitle}</p>}
        {children}
      </div>
    </section>
  )
}
